
import Vue from "vue";
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

export default {
data() {
return {
showRule: false
};
},
components: {
XButton,
Popup
},
methods: {
toEnter() {
this.$router.push({
name: "activityEnter"
});
},
toRule() {
this.showRule = true;
},
closeRule() {
this.showRule = false;
}
}
};




